.color-filter {
  display: flex;
  flex-direction: row;
  grid-gap: 10px;
  gap: 10px;
  justify-content: center;
  flex-wrap: wrap;
}

.color-filter__header {
  width: 100%;
  text-align: center;
  font-family: var(--header-font-family);
  font-size: 18px;
  line-height: 20px;
  color: white;
}

.color-filter__lbl {
  position: relative;
  width: 40px;
  height: 40px;
  cursor: pointer;
}
.color-filter__lbl:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.color-filter__check {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  visibility: hidden;
  width: 40px;
  height: 40px;
}

.color-filter__icon {
  position: relative;
  display: block;
  margin: 0 auto;
  margin-top: 15px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: white;
  box-shadow: 0 0 15px white;
  transition: border-color 0.3s;
}

.color-filter__icon-red {
  background-color: red;
  box-shadow: 0 0 15px red;
}

.color-filter__icon-green {
  background-color: greenyellow;
  box-shadow: 0 0 15px greenyellow;
}
.color-filter__icon-blue {
  background-color: blue;
  box-shadow: 0 0 15px blue;
}
.color-filter__icon-yellow {
  background-color: gold;
  box-shadow: 0 0 15px gold;
}
.color-filter__icon-l {
  margin-top: 0;
  width: 40px;
  height: 40px;
}
.color-filter__check:not(:checked) ~ .color-filter__icon {
  box-shadow: inset 0 0 10px black;
}
@media (max-width: 1200px) {
  .color-filter__header {
    text-align: center;
  }
}

.span-input {
  position: relative;
  margin: 10px;
  width: 440px;
}

.span-input__track {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  top: 9px;
  background-color: var(--white-text);
}

.span-input__min,
.span-input__max {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
}
.span-input__min::-webkit-slider-thumb,
.span-input__max::-webkit-slider-thumb,
.span-input__min::-moz-range-thumb,
.span-input__max::-moz-range-thumb {
  -webkit-appearance: none;
          appearance: none;
  opacity: 0.5;
}
.span-input__min::-webkit-slider-runnable-track,
.span-input__max::-webkit-slider-runnable-track {
  height: 0;
  -webkit-appearance: none;
}

.span-input__text {
  position: absolute;
  top: -14px;
  font-family: var(--text-font-family);
  color: var(--white-text);
  font-size: 14px;
}
.span-input__max-text {
  right: 0;
}

.span-input__min-text {
