.lights {
  position: absolute;
  bottom: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  pointer-events: none;
}
.lights__top {
  position: relative;
  top: calc(-5% - 20px);
  left: calc(50% - 20px);
}
.lights__top-center {
  position: relative;
  top: calc(-15% - 10px);
  left: calc(50% - 50px);
}
.lights__center {
  position: relative;
  top: calc(-20% - 85px);
  left: calc(50% - 70px);
}

.lights__down {
  position: relative;
  top: calc(-25% - 160px);
  left: calc(50% - 110px);
}
.lights__lamp {
  display: block;
  width: 5px;
  height: 12px;
  border-radius: 50%;
  pointer-events: none;
}
.lights__top .lights__lamp {
  position: absolute;
  -webkit-transform-origin: 30px 60px;
          transform-origin: 30px 60px;
}
.lights__top-center .lights__lamp {
  position: absolute;
  -webkit-transform-origin: 50px 80px;
          transform-origin: 50px 80px;
}
.lights__center .lights__lamp {
  position: absolute;
  -webkit-transform-origin: 80px 130px;
          transform-origin: 80px 130px;
}

.lights__down .lights__lamp {
  position: absolute;
  -webkit-transform-origin: 120px 180px;
          transform-origin: 120px 180px;
}
.lights__lamp-white {
  -webkit-animation: flash-white 2s infinite;
          animation: flash-white 2s infinite;
}

.lights__lamp-red {
  -webkit-animation: flash-red 1.2s infinite;
          animation: flash-red 1.2s infinite;
}

.lights__lamp-green {
  -webkit-animation: flash-green 1.4s infinite;
          animation: flash-green 1.4s infinite;
}
.lights__lamp-blue {
  -webkit-animation: flash-blue 1.6s infinite;
          animation: flash-blue 1.6s infinite;
}
.lights__lamp-yellow {
  -webkit-animation: flash-yellow 1.8s infinite;
          animation: flash-yellow 1.8s infinite;
}

@-webkit-keyframes flash-red {
  0%,
  100% {
    background: rgba(255, 0, 0, 1);
    box-shadow: 0 0 15px red;
  }
  50% {
    background: rgba(255, 0, 0, 0.3);
    box-shadow: 0 0 1px red;
  }
}

@keyframes flash-red {
