.snowflakes-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
}
.snowflake {
  position: absolute;
  z-index: 5;
  top: -20px;
  pointer-events: none;
  width: 10px;
  height: 10px;
  fill: white;
  -webkit-transform-origin: 50px 50px;
          transform-origin: 50px 50px;
  -webkit-transform: rotate(90);
          transform: rotate(90);
}

.snowflake:nth-child(1n) {
  -webkit-animation: falling 3s ease-out infinite;
          animation: falling 3s ease-out infinite;
}

.snowflake:nth-child(2n) {
  -webkit-animation: falling 5s ease-out infinite, rotate 5s linear infinite;
          animation: falling 5s ease-out infinite, rotate 5s linear infinite;
}

.snowflake:nth-child(3n) {
  -webkit-animation: falling 4s ease-out infinite;
          animation: falling 4s ease-out infinite;
}
.snowflake:nth-child(7n) {
  width: 20px;
  height: 20px;
}
@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}
@keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}

@-webkit-keyframes falling {
  0% {
    top: -10px;
  }
  100% {
    top: 100%;
  }
}

@keyframes falling {
