.small-toys-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  min-height: 350px;
  grid-gap: 10px;
  gap: 10px;
  justify-content: flex-start;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.5);
