.snow-button,
.music-button {
  width: 50px;
  height: 50px;
  fill: white;
  margin: 10px;
}
.snow-button:hover,
.music-button:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.snow-button__on {
  -webkit-animation: rotate 5s linear infinite;
          animation: rotate 5s linear infinite;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.music-button__off,
.snow-button__off {
