.App {
  text-align: center;
  background-image: url("assets/bg.jpg");
  min-width: 100%;
  min-height: 100%;
  display: flex;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.root {
  background-image: var(--background);
}

